<template>
  <div class="main">
   <!-- 没有企业 -->
   <div class="No">
        <img src="./images/cart-bind.5c49a09.png" alt="">
        <div>
          <p>
      绑定企业后，可将药品加入到购物车进行结算
      </p>
      <van-button class="btn"  type="info" @click="tz">绑定企业</van-button>
        </div>
   </div>
  </div>
</template>

<script>

  export default {
    name: "index",
    components:{

    },
    methods:{
      tz(){
        this.$router.push('/Enterprise')
      }
    }
  }
</script>

<style scoped lang="less">
.main{
  position: absolute;
  top:  0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #f4f4f5;
}
.No{
  width: 100%;
  text-align: center;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%,-50%);
  img{
    width: 4.373333rem;
  }
  p{
    color: #7D7E7F;
    font-size: 0.373333rem;
  }
  .btn{
        margin-top: 1.866667rem;
    width: 4.48rem;
    background: linear-gradient(270deg,#4373ea,#439dea);
    box-shadow: 0 0.106667rem 0.16rem 0 rgba(67,115,234,.3);
    border-radius: 1.066667rem;
    position: relative;
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding-left: .373333rem;
    padding-right: .373333rem;
    box-sizing: border-box;
    font-size: .48rem;
    text-align: center;
    text-decoration: none;
    color: #fff;
    line-height: 2.33333333;
    // border-radius: .133333rem;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    overflow: hidden;
  }
}
</style>



